function getStories(type, offset, amount, term) {
	switch (type) {
		case 'search':
			return getSearchStories(offset, amount, term);
			break;
		case 'topStories':
			return getTopStories(offset, amount);
			break;
		default:
			return getTopStories(offset, amount);
	}
}

function getTopStories(offset, amount) {
	return fetch(topStoriesUrl + '?startFrom=' + offset + '&numberOfArticles=' + amount).then(function (response) {
		return response.json();
	});
}

function getSearchStories(offset, amount, term) {
	return fetch(searchStoriesUrl + '?startFrom=' + offset + '&numberOfArticles=' + amount + '&keyword=' + term).then(function (response) {
		return response.json();
	});
}

